.card {
    background: #fff;
    border-radius: 2px;
    height: 200px;
    margin: 1rem;
    position: relative;
    float: left;
    width: 200px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
   
  }
  
  .card > .img-container {
 
    overflow: hidden;
  
    background:#6CADDC;
  }
  .card > .img-container > img {
    width: 100%;
    height:200px;
  }


